<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <img :src="album.blurPicUrl" alt="" />
      </div>
      <div class="text">
        <p>{{ album.name }}</p>
        <i>Album by {{ artist }}</i>
        <i>{{ album.publishTime | dateFormat }}</i>
        <span class="desc">{{ album.description | ellipsis }}</span>
        <el-button type="primary" round @click="playALLSongs"
          >播放全部</el-button
        >
      </div>
    </div>
    <div class="main">
      <Table :songs="songs"></Table>
    </div>
  </div>
</template>

<script>
import Table from "@/components/Table.vue";
export default {
  components: {
    Table,
  },
  props: ["id"],
  data() {
    return {
      album: {},
      artist: "",
      songs: [],
    };
  },
  methods: {
    async getAlbum() {
      const { data: res } = await this.$http.get("/album", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.album = res.album;
      this.songs = res.songs;
      this.artist = res.album.artist.name;
    },
    playALLSongs() {
      this.$store.commit("setALLSongs", this.songs);
    },
  },
  created() {
    this.getAlbum();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    margin: 30px 0;
    display: flex;
    .photo {
      width: 250px;
      height: 250px;
      margin-right: 50px;
      img {
        width: 250px;
        height: 250px;
        border-radius: 8%;
      }
    }
    .text {
      height: 250px;
      flex: 1;
      p {
        font-size: 30px;
        font-weight: bold;
      }
      i {
        display: block;
        margin: 10px 0;
        color: #303133;
        font-weight: bold;
      }
      .desc {
        display: block;
        margin-bottom: 30px;
        font-size: 15px;
        color: #606266;
      }
    }
  }
}
</style>