<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <img :src="playlist.coverImgUrl" alt="" />
      </div>
      <div class="text">
        <p>{{ playlist.name }}</p>
        <div class="tag">
          <span v-for="(item, index) in playlist.tags" :key="index"
            >#{{ item }}</span
          >
        </div>
        <span class="desc">{{ playlist.description | ellipsis }}</span>
        <el-button type="primary" round @click="playALLSongs">播放全部</el-button>
      </div>
    </div>
    <div class="main">
      <Table :songs="songs"></Table>
    </div>
  </div>
</template>

<script>
import Table from "@/components/Table.vue";
export default {
  components: {
    Table,
  },
  props:['id'],
  data() {
    return {
      playlist: {},
      songs: [],
      loading: true,
    };
  },
  methods: {
    async getPlaylist() {
      const { data: res } = await this.$http.get("/playlist/detail", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.playlist = res.playlist;
      this.songs = res.playlist.tracks;
      this.loading = false;
    },
    playALLSongs(){
      this.$store.commit("setALLSongs", this.songs);
    }
  },
  created() {
    this.getPlaylist();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    margin: 30px 0;
    display: flex;
    .photo {
      width: 250px;
      height: 250px;
      margin-right: 50px;
      img {
        width: 250px;
        height: 250px;
        border-radius: 8%;
      }
    }
    .text {
      height: 250px;
      flex: 1;
      p {
        font-size: 30px;
        font-weight: bold;
      }
      .tag {
        margin: 15px;
        span {
          margin-right: 10px;
          color: #303133;
        }
      }
      .desc {
        display: block;
        margin-bottom: 30px;
        font-size: 15px;
        color: #606266;
      }
    }
  }
}
</style>