<template>
  <div class="container">
    <el-tabs value="first" stretch>
      <el-tab-pane label="相关歌曲" name="first">
        <Table :songs="songs"></Table>
      </el-tab-pane>
      <el-tab-pane label="相关歌手" name="second">
        <div class="artist">
          <ul>
            <li v-for="(item, index) in artists" :key="index">
              <router-link :to="`/artist/${item.id}`">
                <img :src="item.img1v1Url" />
                <span>{{ item.name }}</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-tab-pane>
      <el-tab-pane label="相关专辑" name="third">
        <div class="album">
          <ul>
            <li v-for="(item, index) in albums" :key="index">
              <router-link :to="`/album/${item.id}`">
                <img :src="item.blurPicUrl" alt="" />
                <span>{{ item.name }}</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-tab-pane>
      <el-tab-pane label="相关歌单" name="fourth">
        <div class="playlist">
          <ul>
            <li v-for="(item, index) in playlists" :key="index">
              <router-link :to="`/playlist/${item.id}`">
                <img :src="item.coverImgUrl" />
                <span>{{ item.name }}</span>
              </router-link>
            </li>
          </ul>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import Table from "@/components/Table.vue";
export default {
  components: {
    Table,
  },
  props: ["keywords"],
  data() {
    return {
      songs: [],
      artists: [],
      playlists: [],
      albums: [],
    };
  },
  methods: {
    async getSongs() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.keywords,
          type: "1",
        },
      });
      // console.log(res);
      this.songs = res.result.songs;
    },
    async getArtists() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "100",
        },
      });
      // console.log(res);
      this.artists = res.result.artists;
    },
    async getAlbums() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "10",
        },
      });
      // console.log(res);
      this.albums = res.result.albums;
    },
    async getPlaylists() {
      const { data: res } = await this.$http.get("/cloudsearch", {
        params: {
          keywords: this.$route.params.keywords,
          type: "1000",
        },
      });
      // console.log(res);
      this.playlists = res.result.playlists;
    },
  },
  created() {
    this.getSongs();
    this.getArtists();
    this.getAlbums();
    this.getPlaylists();
  },
  watch: {
    keywords: {
      handler() {
        this.getSongs();
        this.getArtists();
        this.getAlbums();
        this.getPlaylists();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .artist {
    ul {
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        overflow: hidden;
        margin: 20px;
        a {
          img {
            width: 150px;
            height: 150px;
            border-radius: 50%;
          }
          span {
            display: block;
            width: 150px;
            margin-top: 10px;
            text-align: center;
            font-size: 19px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .album {
    ul {
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        margin: 10px 20px;
        overflow: hidden;
        a {
          img {
            width: 150px;
            height: 150px;
            border-radius: 8%;
          }
          span {
            display: block;
            width: 150px;
            height: 50px;
            font-size: 15px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .playlist {
    ul {
      li {
        display: inline-block;
        width: 150px;
        height: 200px;
        margin: 10px 20px;
        overflow: hidden;
        a {
          img {
            width: 150px;
            height: 150px;
            border-radius: 8%;
          }
          span {
            display: block;
            width: 150px;
            height: 50px;
            font-size: 15px;
            font-weight: bold;
          }
        }
      }
    }
  }
}
</style>

