<template>
  <div v-if="$store.state.songInfo.id">
    <div class="show">
      <div class="info">
        <img :src="$store.state.songInfo.al.picUrl" alt="" />
        <div class="text">
          <span>{{ $store.state.songInfo.name }}--</span>
          <span v-for="(item, index) in $store.state.songInfo.ar" :key="index"
            >{{ item.name }}
          </span>
        </div>
      </div>
      <div class="control">
        <button type="button">
          <i class="el-icon-d-arrow-left" @click="previous"></i>
        </button>
        <button type="button" @click="handlePlay">
          <i v-if="playing" class="el-icon-video-pause"></i>
          <i v-else class="el-icon-video-play"></i>
        </button>
        <button type="button" @click="next">
          <i class="el-icon-d-arrow-right"></i>
        </button>
      </div>
      <button type="button" @click="drawerVisible = true">
        <i class="el-icon-s-operation"></i>
      </button>
    </div>
    <audio
      :src="`https://music.163.com/song/media/outer/url?id=${$store.state.songInfo.id}.mp3`"
      autoplay
      ref="audio"
    ></audio>
    <el-drawer title="播放列表" :visible.sync="drawerVisible">
      <el-table
        :data="$store.state.playlist"
        :row-key="getRowKey"
        highlight-current-row
        :current-row-key="$store.state.songInfo.id"
        style="width: 100%"
      >
        <el-table-column prop="name" label="歌曲"></el-table-column>
        <el-table-column label="歌手">
          <template slot-scope="scope">
            <span v-for="(item, index) in scope.row.ar" :key="index"
              >{{ item.name }}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="时长">
          <template slot-scope="scope">
            <span>{{ scope.row.dt | timeFormat }}</span>
          </template>
        </el-table-column>
      </el-table>
    </el-drawer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      playing: false,
      drawerVisible: false,
    };
  },
  watch: {
    "$store.state.songInfo": {
      handler() {
        this.playing = true;
      },
    },
  },
  methods: {
    getRowKey(row) {
      return row.id;
    },
    handlePlay() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
      } else {
        this.$refs.audio.pause();
      }
      this.playing = !this.$refs.audio.paused;
    },
    previous() {
      const index = this.$store.state.playlist.findIndex((item) => {
        return item.id === this.$store.state.songInfo.id;
      });
      if (index > 0) {
        // console.log('pre');
        this.$store.state.songInfo = this.$store.state.playlist[index - 1];
      }
    },
    next() {
      const index = this.$store.state.playlist.findIndex((item) => {
        return item.id === this.$store.state.songInfo.id;
      });
      if (index < this.$store.state.playlist.length - 1) {
        // console.log('next');
        this.$store.state.songInfo = this.$store.state.playlist[index + 1];
      }
    },
  },
};
</script>

<style lang="less" scoped>
.show {
  padding: 0 50px;
  display: flex;
  justify-content: space-between;
  .info {
    display: flex;
    align-items: center;
    img {
      width: 60px;
      height: 60px;
    }
    .text {
      margin-left: 15px;
      span {
        font-size: 15px;
        font-weight: bold;
      }
    }
  }
  .control {
    display: flex;
    button {
      margin-right: 30px;
      border: none;
      outline: none;
      background-color: transparent;
      cursor: pointer;
      i {
        font-size: 30px;
      }
    }
  }
  button {
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    i {
      font-size: 30px;
    }
  }
}
// audio {
