import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  songInfo: {},
  playlist: [],
}
const mutations = {
  setSingleSong(state, data) {
    state.songInfo = data
    const index = state.playlist.findIndex((item) => {
      return item.id === data.id;
    });
    if (index !== -1) {
      state.playlist.splice(index, 1)
    }
    state.playlist.push(data)
  },
  setALLSongs(state, data) {
    state.songInfo = data[0]
    state.playlist = [...data]
  }
}

const store = new Vuex.Store({
  state,
  mutations
})

export default store
