<template>
  <div class="container">
    <div class="header">
      <div class="photo">
        <img :src="artist.picUrl" />
      </div>
      <div class="text">
        <p>{{ artist.name }}</p>
        <span>{{ artist.briefDesc | ellipsis }}</span>
      </div>
    </div>
    <div class="main">
      <el-tabs value="first" stretch>
        <el-tab-pane label="热门作品" name="first">
          <Table :songs="hotSongs"></Table>
        </el-tab-pane>
        <el-tab-pane label="专辑" name="second">
          <div class="album">
            <ul>
              <li v-for="(item, index) in hotAlbums" :key="index">
                <router-link :to="`/album/${item.id}`">
                  <img :src="item.blurPicUrl" alt="" />
                  <span>{{ item.name }}</span>
                </router-link>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <el-tab-pane label="MV" name="third">
          <div class="mv">
            <ul>
              <li v-for="(item, index) in mvs" :key="index">
                <a href="javascript:;">
                  <img :src="item.imgurl" alt="" />
                  <span>{{ item.name }}</span>
                </a>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <el-tab-pane label="详情" name="fourth">
          <div class="desc">
            <span>{{ artist.briefDesc }}</span>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import Table from "@/components/Table.vue";
export default {
  components: {
    Table,
  },
  props: ["id"],
  data() {
    return {
      artist: {},
      hotSongs: [],
      hotAlbums: [],
      mvs: [],
    };
  },
  methods: {
    async getArtist() {
      const { data: res } = await this.$http.get("/artists", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.artist = res.artist;
      this.hotSongs = res.hotSongs;
    },
    async getHotAlbums() {
      const { data: res } = await this.$http.get("/artist/album", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.hotAlbums = res.hotAlbums;
    },
    async getMvs() {
      const { data: res } = await this.$http.get("/artist/mv", {
        params: {
          id: this.id,
        },
      });
      // console.log(res);
      this.mvs = res.mvs;
    },
  },
  created() {
    this.getArtist();
    this.getHotAlbums();
    this.getMvs();
  },
};
</script>

<style lang="less" scoped>
.container {
  padding: 0 50px;
  .header {
    margin: 30px 0;
    display: flex;
    .photo {
      width: 600px;
      height: 300px;
      overflow: hidden;
      margin-right: 50px;
      img {
        width: 600px;
        transform: translate(0, -20%);
      }
    }
    .text {
      flex: 1;
      p {
        font-size: 30px;
        font-weight: bold;
        margin-bottom: 20px;
      }
      span {
        font-size: 15px;
        color: #606266;
      }
    }
  }
  .main {
    .el-table {
      color: #000000;
      button {
        border: none;
        outline: none;
        background-color: transparent;
        cursor: pointer;
        i {
          font-size: 20px;
        }
      }
    }
    .album {
      ul {
        li {
          display: inline-block;
          width: 150px;
          height: 200px;
          margin: 10px 20px;
          overflow: hidden;
          a {
            img {
              width: 150px;
              height: 150px;
              border-radius: 8%;
            }
            span {
              display: block;
              width: 150px;
              height: 50px;
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
    }
    .mv {
      ul {
        li {
          display: inline-block;
          width: 240px;
          height: 210px;
          overflow: hidden;
          margin: 10px 20px;

          a {
            img {
              width: 240px;
              height: 160px;
            }
            span {
              display: block;
              width: 240px;
              height: 50px;
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
    }
    .desc {
      padding: 20px;
      font-size: 15px;
    }
  }
}
</style>